* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  color: #343a40;
  line-height: 1;
}
.goal {
  width: 700px;
  margin: 24px auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.open {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
  padding: 20px 24px;
  padding-right: 48px;
  border-top: 4px solid #fff;
  border-bottom: 4px solid #fff;
}
.number {
  font-size: 24px;
  font-weight: 500;
  color: #ced4da;
}

.goal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
}

.title,
.rate {
  font-size: 24px;
  font-weight: 500;
}

.content-box {
  grid-column: 2 / -1;
  padding-bottom: 16px;
  line-height: 1.6;
}
.content-box ul {
  color: #868e96;
  margin-left: 16px;
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
/* OPEN STATE */
.open {
  border-bottom: 4px solid #696969; /* Green */
}
.open .title {
  flex: 1; /* This will push the rate to the right */
  color: #696969; /* Green */
}
.open .rate {
  color: #696969; /* Green */
  text-align: right;
}
.item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  column-gap: 24px;
  row-gap: 32px;
  align-items: center;
}
/* Center align form elements in a row */
form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 24px;
}
/* Style the input field */
form input[type="text"] {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ced4da;
  border-radius: 4px;
}
/* Style the button */
button {
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #696969; /* Green */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
/* Hover effect for the button */
button:hover {
  background-color: #585858; /* Darker Green */
}

.minis {
  margin-bottom: 16px;
}
.completed {
  border-bottom-color: #1e90ff; /* Dodger Blue */
}
.completed,
.completed .title,
.completed .rate,
.completed .content-box,
.completed .minis {
  color: #1e90ff; /* Dodger Blue */
}
.completed button {
  background-color: #1e90ff; /* Dodger Blue */
}
.completed button:hover {
  background-color: #1c86ee; /* Slightly Darker Blue */
}
